/* Image */


	.image {
		border: 0;
		display: inline-block;
		position: relative;
	}

		.image img {
			display: block;
		}

		.image.left, .image.right, .image.center {
			max-width: 40%;
		}


			.image.left img, .image.right img, .image.center img {
				width: 100%;
			}

			@media screen and (max-width: 480px) {

				.image.left, .image.right {
					max-width: 100%;
					width: 100%;
				}

			}

		.image.left {
			float: left;
			margin: 0 1.5em 1em 0;
			top: 0.25em;
		}

			@media screen and (max-width: 480px) {

				.image.left {
					float: none;
					margin: 0 0 1em 0;
				}

			}

		.image.right {
			float: right;
			margin: 0 0 1em 1.5em;
			top: 0.25em;
		}

			@media screen and (max-width: 480px) {

				.image.right {
					float: none;
					margin: 0 0 1em 0;
				}

			}
		
		.image.center {
			display: block;
			margin-left: auto;
			margin-right: auto;
			top: -1.5em;
			}
	
				@media screen and (max-width: 480px) {
	
					.image.center {
						float: none;
						margin-left: auto;
						margin-right: auto;
					}
	
				}	

		.image.fit {
			display: block;
			margin: 0 0 2em 0;
			width: 100%;
		}

			.image.fit img {
				width: 100%;
			}

		.image.main {
			display: block;
			margin: 0 0 3em 0;
			width: 100%;
