		transform: translateY(1em);
	}

	#banner {
		margin-left: 0.75px;
		display: -ms-flexbox;
		-ms-flex-pack: center;
		-ms-flex-align: center;
		padding: 8em 0 6em 0;
		-moz-align-items: center;
		-ms-align-items: center;
