* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

header > h3 {
    color: ghostwhite;
    text-align: center;
    text-shadow: 1px 1px black;
    font-family: Arial, Helvetica, sans-serif;
}

.score-board {
    margin: 20px auto;
    border: 3px solid black;
    border-radius: 4px;
    text-align: center;
    width: 350px;
    color: orange;
    text-shadow: 1px 1px black;
    font-size: 46px;
    font-weight: bold;
    padding: 15px 20px;
    font-family: Arial, sans-serif;
    position: relative;
}

button#reset-game.button.button-block{
    font-size: 25px;
    text-shadow: 1px 1px rgb(77, 74, 74);
}

button#H-opponent.button.button-block{
    font-size: 20px;
    text-shadow: 1px 1px rgb(77, 74, 74);
    margin-right: 10px;
    color: white;
}

button#C-opponent.button.button-block{
    font-size: 20px;
    text-shadow: 1px 1px rgb(77, 74, 74);
    margin-left: 10px;
    color: white;
}

.badge {
    background: #0d1217;
    color: #19B5FE;
    font-size: 21px;
    padding: 10px 20px;
    text-shadow: 1px 1px white;
    font-family: Asap, sans-serif;
}

#user-label {
    position: absolute;
    top: 30px;
    left: -25px;
}

#computer-label {
    position: absolute;
    top: 30px;
    right: -30px;
}

.message {
    font-size: 30px;
    color: rgb(0, 0, 0);
    text-shadow: 2px 2px rgb(255, 255, 255);
}

div.play  {
    text-align: center;
    font-size: 25px;
    font-weight: bold;
    font-style: italic;
    color: black;
    text-shadow: 2px 2px white;
}

.reset {
    text-align: center;
    color: white;
}

.message > p {
    text-align: center;
    font-weight: bold;
    font-family: Arial, sans-serif;
}

.choice:hover {
    cursor: pointer;
    background: #24272E;
}

table#notdisabled.tictactoetable td.tictactoe-cell, table#notdisabled.tictactoetable td.tictactoe-cell-win-player1, table#notdisabled.tictactoetable td.tictactoe-cell-win-player2 {
        padding: 0;
        cursor: pointer;
}

table#disabled.tictactoetable td.tictactoe-cell, table#disabled.tictactoetable td.tictactoe-cell-win-player1, table#disabled.tictactoetable td.tictactoe-cell-win-player2{
    padding: 0;
    cursor: not-allowed;  
}

td#disabled.tictactoe-cell:hover, td#disabled.tictactoe-cell-win-player1:hover,td#disabled.tictactoe-cell-win-player2:hover{
    cursor: not-allowed;  
}
/* winCondition */
td#disabled.tictactoe-cell-win-player1{
    background-color: #45b3e7;
}

td#disabled.tictactoe-cell-win-player2{
    background-color: rgba(243, 111, 94, 0.76);
}

td#disabled-tie{
    background-color: rgba(39, 180, 39, 0.658);
    padding: 0;
    cursor: not-allowed; 
}

td#disabled{
    cursor: not-allowed; 
    padding: 0;
    color:black;
    text-shadow: 2px 2px white;
}

td  {
    border:  10px solid #333;
    height:  calc(500px / 3);
    width:  33.33%;
    text-align:  center;
    vertical-align:  middle;
    font-size:  70px;
    cursor: pointer;
}

div#board.Boxes {
    width:500px;
    height:500px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 35px;
}


table.tictactoetable {
    border-collapse: collapse;
    margin-top:50px;
}

div#board.Boxes table tbody tr {
    border: none;
}

table.tictactoetable tr:first-child td {
    border-top: 0;
}

table.tictactoetable tr:last-child td {
    border-bottom: 0;
}

table.tictactoetable tr td:first-child {
    border-left: 0;
}

table.tictactoetable tr td:last-child {
    border-right: 0;
}


@media only screen
and (min-device-width : 375px)
and (max-device-width : 736px)
 { 
    
    .score-board {
        width: 250px;
        font-size: 35px;
    }

    div.message p#game-message, div.play p#game-message {
        font-size: 15px;
    }

    button#H-opponent.button.button-block, button#C-opponent.button.button-block{
        font-size: 15px;
        width:100px;
      
    }

    button#reset-game.button.button-block{
       width:138.02px;
       text-shadow: 1px 1px rgb(77, 74, 74);
    }
  
    .badge { 
        font-size: 20px;
    }

    #user-label {
        top: 15px;
    }
    
    #computer-label {
        top: 15px;
        
    }

    span#reset__game{
        font-size: 16px;
    }
    
    div#board.Boxes {
        width:375px;
        height:375px;
        margin-bottom: 50px;
    }

    td  {
        height:  calc(375px / 3); 
    }

}

.choice:hover {
    cursor: pointer;
    background: #24272E;
}

div.connectFourBoard{
    background: #0074B3;
    box-shadow: 5px 10px rgba(25, 60, 78, 0.74) ;
    width: 700px;
    height: 625px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 25px;
    margin-bottom: 25px;
}

div#game-board {
    cursor: pointer;
}

div#game-board-disabled {
    cursor: not-allowed;
}

svg#connectFourSVG{
    width:100px;
    height: 100px;
}

.column {
    width: 14.2857142857%;
    height: 14.2857142857%;
	display: inline-block;
}

.column:hover circle.free-disabled{
    fill: rgba(235, 84, 84, 0.712);
    cursor: not-allowed;
}

.column:hover circle.free{
	fill: rgba(112, 250, 20, 0.555);
}

circle.free, circle.free-disabled {
    fill: white;
}

circle.game_over {
	fill: rgb(8, 8, 8);
}

circle.red {
	fill: red;
}

circle.yellow {
	fill: #ffff00
}

@media only screen
and (min-device-width : 375px)
and (max-device-width : 667px)
and (orientation : portrait)
 {
    div.connectFourBoard{
       
        width: 100%;
        height: 250px;
     
    }
    
    svg#connectFourSVG{
        width:38px;
        height: 38px;
    }

 }
div.card,div.card-body.mx-auto{
    background-color: white;
}

.pokemon-data{
    margin: 25px 70px 70px 70px;
}

.individual__card{
    opacity: 0.95;
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1); 
    align-self: center;
    -moz-user-select: none; /* Mozilla-specific values */
    -ms-user-select: none; /* Microsoft-specific values */
    -webkit-user-select: none; /* WebKit-specific values */
    user-select: none; /* Keyword values */
    -o-user-select: none; 
}

div.pokemon__image{
    display: block;
    width: 260px;
    height: 260px;
}

h5.card-header{
    width:100%;
}

img#pokemonImg{
    height: 100%;
    width:100%;
}

img#pokemonGif{
    height: 60%;
    width:60%;
    margin-left: 50px;
    margin-top: 50px;
}

h3.card-title{
    text-align: center;
    text-shadow: 2px 2px 2px darkgrey;
    color:black;
}

@media only screen and (width: 768px) and (height:1024px) {
    h3{ 
        font-size: 15px;
    }
}

.link-hover :hover, .link-hover:focus , .link-hover:visited ,.link-hover:link ,.link-hover:active  {
    box-shadow: 0 14px 28px rgba(255,255,255, 0.9), 0 10px 10px rgba(255,255,255, 0.9);
    color:black;
    text-decoration: none;
    
}


